.login--container{
    position: absolute;
    display : flex;
    top : 160px;
    left : 358px;
    width: 700px;
    height : 300px;
    border: 1px solid black;
    background-color: rgb(16, 17, 35);
    box-shadow: 0px 2.98256px 7.4564px rgba(255, 255, 255, 0.631);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    box-shadow: 5px 5px 5px 5px rgb(177, 182, 184);

}

.login--info{
    width : 400px;
    color : rgb(239, 236, 251);
    text-align : center;
}

.login--logo {
    position : relative;
    left : -50px;
    font-size :150px;
    margin : 0 ;
}
.login--howto{
    position : relative;
    width : 250px;
    font-size: 20px;
    left : 25px;
}
h5{
    margin : 0;
    position : relative;
    width : 250px;
    font-size: 15px;
    left : 25px;
    color: rgb(167, 164, 164);
}

.login--form{
    position : relative;
    width: 350px;
    display  :block;
}
.login--emailInput{
    position: relative;
    border-radius: 7px;
    width : 300px;
    height : 30px;
    top : 40px;
    padding-left: 10px;
}
.login--passwordInput{
position: relative;
border-radius: 7px;
width : 300px;
height : 30px;
top : 70px;
padding-left : 10px;

}
.login--login{
    position : relative;
    width : 200px;
    height : 35px;
    border-radius: 7px;
    top : 100px;
    left : 50px;
    padding-left: 5px;
    color : white;
    background-color :rgb(28, 29, 58);
}
.login--register{
    position : relative;
    width : 200px;
    height : 35px;
    border-radius: 7px;
    top : 120px;
    left : 50px;
    padding-left : 5px; 
    color : white;
    background-color :rgb(16, 17, 35)
}
h6{
    margin-left: -90px;
    font-size: 10px;
}

.login--messages{
    position: absolute;
    top : 50px;
    left : 350px;
    width: 700px;
    border: 1px solid black;
    background-color: rgb(16, 17, 35);
    box-shadow: 0px 2.98256px 7.4564px rgba(255, 255, 255, 0.631);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    box-shadow: 5px 5px 5px 5px rgb(177, 182, 184);
    color : white;
}